<template>
	<div class="row">
		<div id="home" class="col-sm-12 text-center">
			<div class="background">
				<h1>欢迎品尝</h1>
				<h2>这里有你喜欢的</h2>
				<button @click="goToMenu" class="btn btn-success">跳转</button>
			</div>
		</div>

		
	</div>
</template>	
<script>
	export default{
		methods:{
			goToMenu(){
				this.$router.push({name:'menuLink'})
			}
		}
	}
</script>

<style scoped>
#home{
	background:url('../../src/assets/pizza.jpg');
	height: 85vh;
	padding: 10%;
}
h1,h2{
	margin: 6%;
}
.background{
	background: #eee;
	opacity: 0.8;
	max-width: 70vw;
	margin: 0 auto;
	padding: 20px 0;
}
.row{margin: 0;}
</style>


